<template>
	<div>
		<v-layout row>
		    <v-flex xs12>
		      <v-card>
		        <v-toolbar color="cyan" dark>
		          <v-toolbar-title class="title">Trabajos Lautaro Umpierrez</v-toolbar-title>
		        </v-toolbar>
		        <v-list two-line>
		          <template v-for="(item, index) in items" class="item" v-on:click="go(item.url)">
		            <v-subheader v-if="item.header" :key="item.header">{{ item.header }}</v-subheader>
		            <v-divider v-else-if="item.divider" :inset="item.inset" :key="index"></v-divider>
		            <v-list-tile v-on:click="go(item.url)" avatar v-else :key="item.title">
		              <v-list-tile-avatar>
		                <img :src="item.img">
		              </v-list-tile-avatar>
		              <v-list-tile-content>
		                <v-list-tile-title v-html="item.title"></v-list-tile-title>
		                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
		              </v-list-tile-content>
		            </v-list-tile>
		          </template>
		        </v-list>
		      </v-card>
		    </v-flex>
		  </v-layout>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				items:[
					{title: 'Biologa',subtitle:'Trabajos de biologia.',url:'/biology',img:'http://www.basicknowledge101.com/photos/2016/biology_sketches.jpg'}
				]
			}
		},
		methods:{
			go(url)
			{
				this.$router.push(url);
			}
		},
		computed:{

		}
	}
</script>
<style lang="scss">
	.title{
		color: #fff !important
	}
</style>
			


